import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { UserState } from '../model';
import Box from './Box';
import ActiveInfo from '../../../components/ActiveInfo';
import send_plane from '../../../assets/icons/send_plane.svg';

interface TrainBasicDataProps {
  user: UserState
}

const TrainBasicData: React.SFC<TrainBasicDataProps> = (props) => {
  const { user } = props;
  const { userData } = user;
  const [curriculums, setCurriculums] = useState<string[]>([]);

  useEffect(() => {
    if (userData) {
      const learns = [];

      userData.feimooc.learn.planCamp.forEach((i: any) => {
        if (!learns.includes(i.title)) {
          learns.push(i.title)
        }
      })

      userData.feimooc.learn.xbCamp.forEach((i: any) => {
        if (!learns.includes(i.title)) {
          learns.push(i.title)
        }
      })

      console.log(learns)
      setCurriculums(learns);
    }
  }, [global])

  return (
    <Box title="基础资料">
      <div>
        <ActiveInfo title="渠道来源" value={userData && userData.channel} />
        <ActiveInfo title="加好友时间" value={userData && userData.addTime} />
        <ActiveInfo title="报名电话" value={userData && userData.pro.trainingData.phone} />
        <ActiveInfo title="已关公号" value={userData && userData.subscribe} alignItems="flex-start" />
        <ActiveInfo title="企微描述" value={userData && userData.description} />
        <ActiveInfo title="已报课程" alignItems="flex-start" value={curriculums.map((i: string, n: number) => {
          if (n === curriculums.length - 1) {
            return i
          } else {
            return `${i}、`
          }
        })} />
        <ActiveInfo title="所属营期" value={userData && userData.train.training_name} />
        <ActiveInfo title="开营时间" value={userData && `${(userData.train.training_start_time).substring(0, 4)}-${(userData.train.training_start_time).substring(4, 6)}-${(userData.train.training_start_time).substring(6, 8)}`} />
        <ActiveInfo title="归属导师" value={userData && userData.train.consultantCnName} />
        <ActiveInfo title="助教备注" value={userData && userData.customerInfo.porscheMemo} />
        <ActiveInfo title="智诊入口" value={<div style={{display: 'flex', alignItems: 'center', fontWeight: 400, color: '#1890FF'}}>
          发送<img src={send_plane} alt="发送按钮" style={{marginLeft: '5px'}}/>
        </div>} />
      </div>
    </Box>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(TrainBasicData);