import React, { useEffect } from 'react';
import { connect } from 'dva';

import { UserState } from '../../model';
import Mask from '../../../../components/Mask';
import Card from '../Card';
import GroupItem from './GroupItem';
import styles from './index.less';

interface ColleagAndGroupProps {
  visible: boolean;
  onCancel: Function;
  user: UserState;
}

const ColleagAndGroup: React.FC<ColleagAndGroupProps> = (props) => {
  const { visible, onCancel, user } = props;

  useEffect(() => {

  }, [user])

  return (
    <Mask title="同事和群聊" visible={visible} onCancel={()=>onCancel()}>
      <div className={styles.ColleagAndGroupContainer}>
        <div className={styles.ColleagAndGroupItem}>
          <p className={styles.title}>Ta添加的同事 ({user.dataOverview ? user.dataOverview.userList.length : 0})</p>
          <div style={{marginTop: '20px'}}>
            {
              user.dataOverview && user.dataOverview.userList.map((i: any) => {
                return (
                  <Card imgUrl={i.avatar} title={i.name} description={`添加时间：${i.createdTime}`} key={i.userId} />
                )
              })
            }
          </div>
        </div>
        <div className={styles.ColleagAndGroupItem}>
          <p className={styles.title}>Ta所在的群聊 (2)</p>
          <div style={{marginTop: '20px'}}>
            {
              user.dataOverview && user.dataOverview.chatList.map((i: any) => {
                return (
                  <GroupItem owner={i.owner} name={i.name} joinTime={i.joinTime} active={i.self === 'yes'} key={`${i.name}-${i.owner}`} />
                )
              })
            }
          </div>
        </div>
      </div>
    </Mask>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(ColleagAndGroup);