import React, { useState, useEffect } from 'react';
import { connect } from "dva";

import { EncryptDes, SetCookie } from '../../utils/func';
import { fetchTodoList, fetchUserDeparment } from '../../services/detail';
import { tabbersIndex } from '../datas';
import { GlobalState } from '../../models/global';
import { UserState } from './model';
import actions from '../../shared/actions';
import Tabber from './components/Tabber/Tabbar';
import UserCard from './UserCard';
import DataCard from './DataCard';
import DealtCard from './DealtCard';
import TeacherCard from './components/TeacherCard';
import DetailTabs from './DetailTabs';
import AddDealt from './components/AddDealt';
import Remark from './components/Remark';
import TransferCustomers from './components/TransferCustomers';
import styles from './index.less';
import IndexContentContext, { IndexContentContextProps } from './IndexContentContext';

interface IndexProps {
  dispatch: Function,
  history: any,
  global: GlobalState,
}

const Index: React.FC<IndexProps> = (props) => {
  const { dispatch, history, global } = props;
  const [type, setType] = useState<string>('train');
  const [addDealtVisible, setAddDealtVisible] = useState<boolean>(false);
  const [transferVisible, setTransferVisible] = useState<boolean>(false);
  const [teacherVisible, setTeacherVisible] = useState<boolean>(false);
  const [todoLists, setTodoLists] = useState<any[]>([]);
  const [remarkVisible, setRemarkVisible] = useState<boolean>(false);
  const [remarkType, setRemarkType] = useState<string>('');
  
  const handleChangeType = (val: string) => {
    setType(val);
    initUserData(global.unionid, global.externaluserid, val)
  }

  const getContext: () => IndexContentContextProps = () => {
    return {
      type,
      setType: (val) => handleChangeType(val),
      openRemark: (val) => {
        setRemarkType(val);
        setRemarkVisible(true);
      }
    }
  }

  const handleTabber = (value) => {
    if (value === 2) {
      setAddDealtVisible(true);
    } else if (value === 3) {
      setTransferVisible(true);
    } else if (value === 4) {
      setTeacherVisible(true);
    } else if (value === 1) {
      history.push("/followUp")
    }
  }

  const initUserData = (unionid: string, externaluserid: string, kind: string) => {
    // 初始化用户数据
    dispatch({
      type: "user/fetchExternalData",
      payload: {
        unionid,
        externaluserid,
        userId: localStorage.getItem('userId'),
        kind,
      }
    })
  }

  const initDepartment = () => {
    // 初始化理财师部门信息
    dispatch({
      type: "user/fetchUserDeparment",
      payload: {
        userId: localStorage.getItem('userId')
      }
    })
  }

  const initDataOverview = (unionid: string, externaluserid: string) => {
    // 初始化数据概况
    dispatch({
      type: "user/fetchDataOverview",
      payload: {
        unionid,
        externaluserid,
        userId: localStorage.getItem('userId'),
      }
    })
  }

  const initTodoList = () => {
    // 初始化跟进
    fetchTodoList({
      userId: localStorage.getItem('userId'),
      completeStatus: -1
    }).then((res: any) => {
      if (res.code === 200 && res.data) {
        const datas = [];

        res.data.map((i: any) => {
          if (i.complete_status === 0) {
            datas.push(i);
          }
        })
        
        setTodoLists(datas);
      }
    })
  }

  useEffect(() => {
    actions.onGlobalStateChange(state => {
      SetCookie('externaluserid', EncryptDes(state.externaluserid));
      SetCookie('unionid', EncryptDes(state.unionid));
      dispatch({
        type: 'global/saveExternalData',
        payload: {
          unionid: state.unionid,
          externaluserid: state.externaluserid
        }
      })
      initUserData(state.unionid, state.externaluserid, 'train')
      initDataOverview(state.unionid, state.externaluserid)
      initTodoList()
      initDepartment()
    }, true)
  }, [])

  useEffect(() => {
    
  }, [type])
  
  return (
    <div className={styles.IndexContainer}>
      <div className={styles.content}>
        <IndexContentContext.Provider value={getContext()}>
          <UserCard/>
          {
            todoLists.length > 0 && <DealtCard dataSource={todoLists[todoLists.length - 1]} />
          }
          <DataCard/>
          <DetailTabs/>
        </IndexContentContext.Provider>
      </div>
      <AddDealt visible={addDealtVisible} onCancel={()=>setAddDealtVisible(false)}/>
      <TransferCustomers visible={transferVisible} onCancel={()=>setTransferVisible(false)}/>
      <TeacherCard visible={teacherVisible} onCancel={()=>setTeacherVisible(false)}/>
      <Tabber dataSource={type === 'teacher' ? tabbersIndex.teacher : tabbersIndex.customer} onClick={handleTabber}/>
      <Remark visible={remarkVisible} onCancel={()=>setRemarkVisible(false)} type={remarkType} />
    </div>
  )
}

export default connect(({ global, user }: { global: GlobalState, user: UserState }) => ({
  global,
  user,
}))(Index);