import React from 'react';

import { DataSourceProps } from './TabberItem';
import TabberItem from './TabberItem';
import styles from './index.less';

interface TabberProps {
  dataSource: DataSourceProps[],
  onClick?: Function;
}

const Tabber: React.SFC<TabberProps> = (props) => {
  const { dataSource = [], onClick } = props;
  
  return (
    <div className={styles.tabber}>
      <TabberItem dataSource={dataSource} onClick={onClick}/>
    </div>
  )
}

export default Tabber