import React from 'react';

import group from '../../../../assets/images/group.jpg';
import copy from '../../../../assets/icons/copy.svg';
import styles from './index.less';

interface GroupItemProps {
  active?: boolean;
  name: string;
  owner: string;
  joinTime: string;
}

const SlefGroupText = {
  0: '我不在群聊中',
  1: '我已在群聊中',
}

const GroupItem: React.SFC<GroupItemProps> = (props) => {
  const { active, name, owner, joinTime } = props;

  return (
    <div className={styles.groupItem}>
      <img src={group} alt="群图片" className={styles.left} />
      <div className={styles.right}>
        <p className={styles.line1}>{name}</p>
        <p className={styles.line2}>群主: {owner}</p>
        <p className={styles.line3}>
          <span>入群时间: {joinTime}</span>
          <img src={copy} alt="复制" style={{cursor: 'pointer'}} />
        </p>
        <p className={styles.line4}>
          <span className={active ? styles.groupItemStatus + ' ' + styles.active : styles.groupItemStatus} >{SlefGroupText[active ? 1 : 0]}</span>
        </p>
      </div>
    </div>
  )
}

export default GroupItem;