import React from 'react';
import { Tabs } from 'antd-mobile';

import IndexContentContext from '../../IndexContentContext';
import DataTabs from './DataTabs';
import ActiviteFollowUp from './ActiviteFollowUp';
import Explanation from './Explanation';
import Dynamic from './Dynamic';
import styles from './index.less';
interface SmartDetailTabsProps {
  options: any[];
}

const SmartDetailTabs: React.FC<SmartDetailTabsProps> = (props) => {
  const { options } = props;

  return (
    <IndexContentContext.Consumer>
      {context => {
        return (
          <div className={styles.detailTabsContainer}>
            <Tabs tabs={options}>
              {/** 基本资料 */}
              <DataTabs />
              {/** Wealth */}
              <ActiviteFollowUp />
              {/** 风险评估 */}
              <Explanation />
              {/** 客户动态 */}
              <Dynamic />
            </Tabs>
          </div>
        )
      }}
    </IndexContentContext.Consumer>
  )
}

export default SmartDetailTabs;