import React from 'react';

import Mask from '../../../../components/Mask';
import DataItem, { DataItemProps } from './DataItem';
import styles from './index.less';

interface DataDetailProps {
  visible: boolean;
  onCancel: Function;
  dataSource: DataItemProps[];
}

const DataDetail: React.SFC<DataDetailProps> = (props) => {
  const { visible, onCancel, dataSource = [] } = props;
  return (
    <Mask title="数据概况" visible={visible} onCancel={onCancel}>
      <div className={styles.dataDetail}>
        {
          dataSource.map((i: DataItemProps) => {
            return (
              <DataItem title={i.title} value={i.value} hide={i.hide} key={`${i.title}${i.value}`}/>
            )
          })
        }
      </div>
    </Mask>
  )
}

export default DataDetail;