import React, { useState } from 'react';

import TabBtn from '../../components/TabBtn';
import ActiveInfo from '../../../../components/ActiveInfo';
import {
  Train_follow_up,
} from '../../../datas';
import styles from '../index.less';

const TrainActiviteFollowUp: React.FC = () => {
  const [type, setType] = useState<string>('guarantee');

  const handleClick = (value: string) => {
    console.log(value)
    setType(value)
  }

  return (
    <div>
      <div className={styles.headerBox}>
        <TabBtn options={Train_follow_up} onChange={handleClick}/>
      </div>
      <div className={styles.contentBox}>
        <div style={{padding: '0 15px'}}>
          <ActiveInfo title="参与活动" value="是" />
          <ActiveInfo title="当前促成阶段" value="待激活" />
          <ActiveInfo title="下次沟通时间" value="2020/11/25 18:30-19:30" />
          <ActiveInfo title="预估FYP" value="4000元" />
          <ActiveInfo title="主要异议" value="他老公不想买" />
          <ActiveInfo title="执行FYP" value="0元" />
        </div>
      </div>
    </div>
  )
}

export default TrainActiviteFollowUp;