import React from 'react';

import calendar from '../../../../assets/icons/calendar.svg';
import sound from '../../../../assets/icons/sound.svg';
import Timeline from '../../../../components/Timeline';
import styles from './index.less';

const CallRecord:React.SFC = () => {
  return (
    <div style={{display: 'flex', alignItems: 'center', backgroundColor: '#E6F7FF', color: '#1890FF', padding: '0 20px 0 10px'}}>
      <img src={sound} alt="声音"/>00:15:18
    </div>
  )
}

const FollowRecord: React.SFC = () => {
  return (
    <div className={styles.lineRecord}>
      <p>沟通类型：待一促</p>
      <p>预估FYP：4000元</p>
      <p>主要异议：客户老公不想买，觉得是骗人的</p>
      <p>下次沟通时间：2020/11/25 18:30-19:30</p>
      <p>操作人：陈春霞</p>
    </div>
  )
}

const StudyRecord: React.SFC = () => {
  return (
    <div className={styles.lineRecord}>
      <p>上课时长：0小时14分36秒</p>
      <p>打卡时间：2020/08/10 10:34</p>
      <p>打卡心得：这课学到了买保险一定要如实告知免得理赔时出现一些小插曲，耽误了理赔的速度</p>
    </div>
  )
}

const TimeBox: React.FC = () => {
  return (
    <div className={styles.timeLineBox}>
      <div className={styles.timeLineTitle}>
        <img src={calendar} alt="日期" /> 2020/11/17 &nbsp;&nbsp;星期一
      </div>
      <div className={styles.timeContent}>
        <Timeline title="添加好友" time="15:32" content="Ta添加了员工  李军  为好友" />
        <Timeline title="企微通话记录" time="15:32" content={<CallRecord/>} />
        <Timeline title="客户跟进记录" time="15:54" content={<FollowRecord/>} />
        <Timeline title="上课记录" time="15:54" content={<StudyRecord/>} />
      </div>
    </div>
  )
}

export default TimeBox;