import React, { useState } from 'react';

import TabBtn from '../../components/TabBtn';
import {
  TrainBasicData,
  TrainWealthQuestion,
  TrainPulanOperation,
  TrainInfomation,
  TrainPlanning,
} from '../../Boxs';
import {
  Train_information,
} from '../../../datas';
import styles from '../index.less';

const TrainDataTabs: React.FC = () => {
  const [type, setType] = useState<string>('');

  const handleClick = (value: string) => {
    setType(value)
  }

  return (
    <div>
      <div className={styles.headerBox}>
        <TabBtn options={Train_information} onChange={handleClick}/>
      </div>
      {
        type ? type === 'basic_data' && <div className={styles.contentBox}>
          <TrainBasicData />
        </div> : <div className={styles.contentBox}>
          <TrainBasicData />
        </div>
      }
      {
        type ? type === 'wealth_question' && <div className={styles.contentBox}>
          <TrainWealthQuestion/>
        </div> : <div className={styles.contentBox}>
          <TrainWealthQuestion/>
        </div>
      }
      {
        type ? type === 'pulan_operation' && <div className={styles.contentBox}>
          <TrainPulanOperation/>
        </div> : <div className={styles.contentBox}>
          <TrainPulanOperation/>
        </div>
      }
      {
        type ? type === 'facilitating_information' && <div className={styles.contentBox}>
          <TrainInfomation/>
        </div> :  <div className={styles.contentBox}>
          <TrainInfomation/>
        </div>
      }
      {
        type ? type === 'compre_planning' && <div className={styles.contentBox}>
          <TrainPlanning/>
        </div> :  <div className={styles.contentBox}>
          <TrainPlanning/>
        </div>
      }
    </div>
  )
}

export default TrainDataTabs;