import React, { ReactNode } from 'react';

import styles from './index.less';

interface BoxProps {
  title: string | number;
  extra?: string | number | ReactNode;
  children: ReactNode;
}

const Box: React.FC<BoxProps> = (props) => {
  const { children, title, extra } = props;

  return (
    <div className={styles.BoxContainer}>
      <div className={styles.header}>
        <div className={styles.left_box}>
          <span className={styles.left_box_border}/> {title}
        </div>
        <div className={styles.right_box}>{extra}</div>
      </div>
      <div className={styles.bodyer}>
        {children}
      </div>
    </div>
  )
}

export default Box;