import React from 'react';

import Tag from '../Tag';
import styles from './index.less';

interface TagsBoxProps {
  title: string;
  dataSource: string[];
}

const TagsBox: React.SFC<TagsBoxProps> = (props) => {
  const { title, dataSource } = props;

  return (
    <div className={styles.tagsBoxContainer}>
      <p className={styles.tagsTitle}><span className={styles.borderLeft}/>{title}</p>
      <div className={styles.tagsContent}>
        {
          dataSource.map((i: string) => {
            return <Tag value={i} key={i} />
          })
        }
      </div>
    </div>
  )
}

export default TagsBox;