import React from 'react';

import Mask from '../../../../components/Mask';
import Button from '../../../../components/Button';

interface TeacherCardProps {
  visible: boolean;
  onCancel: Function;
}

const TeacherCard: React.SFC<TeacherCardProps> = (props) => {
  const { visible, onCancel } = props;

  return (
    <Mask title="导师名片" visible={visible} onCancel={onCancel}>
      <div>
        <div style={{padding: '20px'}}>
          <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" style={{width: '100%', height: '100%'}}/>
        </div>
        <div style={{padding: '0 20px'}}>
          <Button type="primary">发送</Button>
        </div>
      </div>
    </Mask>
  )
}

export default TeacherCard;