import React from 'react';

import styles from './index.less';

interface CardProps {
  imgUrl: string;
  title: string;
  description: string;
}

const Card: React.SFC<CardProps> = (props) => {
  const { imgUrl, title, description } = props;

  return (
    <div className={styles.CardContainer}>
      <img src={imgUrl} alt="卡片图片" className={styles.left} />
      <div className={styles.right} >
        <p className={styles.line1}>{title}</p>
        <p className={styles.line2}>{description}</p>
      </div>
    </div>
  )
}

export default Card;