import React from 'react';

import Mask from '../../../../components/Mask';
import TagsBox from './TagsBox';

interface DataSourceProps {
  title: string;
  datas: string[];
}

interface TagsListsProps {
  dataSource: DataSourceProps[];
  visible: boolean;
  onCancel: Function;
}

const TagsLists: React.SFC<TagsListsProps> = (props) => {
  const { dataSource = [], visible = false, onCancel } = props;

  return (
    <Mask visible={visible} onCancel={onCancel} position="bottom" title="客户标签">
      <div>
        {
          dataSource.map((i: DataSourceProps) => {
            return (
              <TagsBox title={i.title} dataSource={i.datas} key={i.title} />
            )
          })
        }
      </div>
    </Mask>
  )
}

export default TagsLists;