import React, { useState, useRef, useEffect } from 'react';
import { connect } from 'dva';

import { tabbers, TYPETITLE, GenderIcon } from '../datas';
import { UserState } from './model';
import IndexContentContext from './IndexContentContext';
import TabbarItem from './components/Tabber/TabberItem';
import TagsLists from './components/TagsLists';
import Menus from './components/Menus';
import Tag from './components/Tag';
import switch_swap from '../../assets/icons/switch_swap.svg';
import styles from './index.less';

interface UserCardProps {
  user: UserState,
}

const UserCard: React.FC<UserCardProps> = (props) => {
  const { user } = props;
  const { userData, department } = user;
  const tagsBox: any = useRef();
  const [tags, setTags] = useState<string[]>([]);
  const [tagsDatas, setTagsDatas] = useState<any[]>([
    {
      title: '企业标签',
      datas: []
    },
    {
      title: '个人标签',
      datas: []
    },
  ]);
  const [showAllBtn, setShowAllBtn] = useState<boolean>(false);
  const [menusVisible, setMenusVisible] = useState<boolean>(false);
  const [tagsVisible, setTagsVisible] = useState<boolean>(false);

  const handleMenus = (value, setType) => {
    setType(value);
    setMenusVisible(false);
  }

  const handleTabber = (val) => {
    if (val === 1) {
      // 规划方案
      console.log(department)
    } else if (val === 2) {
      // 客户保单
    } else if (val === 3) {
      // 查看撤单
      window.open('https://bx.planplus.cn/marketing/m/order/order-query');
    } else if (val === 4) {
      // 理赔服务
      window.open('https://report.planplus.cn/policy/#/claim');
    }
  }

  useEffect(() => {
    if (tagsBox) {
      if (tagsBox.current.clientHeight <= 52) {
        setShowAllBtn(false);
      } else {
        setShowAllBtn(true);
      }
    }
  }, [tags])

  useEffect(() => {
    if (userData) {
      const personTags =  [];
      const enterpriseTags = [];
      userData.customer.follow_user.forEach((i: any) => {
        if (i.userid === localStorage.getItem('userId')) {
          setTags(i.tags)
          i.tags.forEach((j: any) => {
            if (j.type === 1) {
              enterpriseTags.push(j.tag_name)
            } else {
              personTags.push(j.tag_name)
            }
          })
        }
      })

      setTagsDatas([{
        title: '企业标签',
        datas: enterpriseTags
      },
      {
        title: '个人标签',
        datas: personTags
      },])
    }
  }, [user])

  return (
    <IndexContentContext.Consumer>
      {context => {
        const { type, setType } = context;

        return (
          <>
            <TagsLists dataSource={tagsDatas} visible={tagsVisible} onCancel={()=>setTagsVisible(false)}/>
            <Menus visible={menusVisible} onCancel={()=>setMenusVisible(false)} onClick={(val)=>handleMenus(val, setType)}/>
            <div className={styles.userCard}>
              <div className={styles.container}>
                <div className={styles.left}>
                  <img src={userData && userData.customer.avatar} />
                </div>
                <div className={styles.right}>
                  <div className={styles.line1}>
                    <div className={styles.line1_left}>
                      <span className={styles.username}>{userData && userData.pro.realname}</span>
                      <img src={userData && GenderIcon[userData.pro.gender]} alt="性别" />
                    </div>
                    <div className={styles.line1_right} onClick={()=>setMenusVisible(true)}>
                      {TYPETITLE[context.type]}<img src={switch_swap} alt="切换菜单"/>
                    </div>
                  </div>
                  <div className={styles.line2}>
                    昵称：{userData && userData.pro.nickname }<span className={styles.tag}>@微信</span>
                  </div>
                  <div className={styles.line3}>
                    沉默时长：{userData && userData.pro.FinanceData.silence}天
                  </div>
                  <div className={styles.tagsContainer}>
                    <div ref={tagsBox} className={styles.tagsBox}>
                      {
                        tags.map((i: any) => {
                          return <Tag value={i.tag_name} key={`${i.group_name}-${i.tag_name}`} />
                        })
                      }
                    </div>
                    {showAllBtn && <span className={styles.showAll} onClick={()=>setTagsVisible(true)}>全部 &gt;</span>}
                  </div>
                </div>
              </div>
              <TabbarItem dataSource={tabbers} color="#0681F4" onClick={handleTabber}/>
            </div>
          </>
        )
      }}
    </IndexContentContext.Consumer>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(UserCard);