import React, { useState } from 'react';
import { Chart, Axis, Geom, Coord, Legend } from 'bizgoblin';

import Box from '../../Boxs/Box';
import TabBtn from '../../components/TabBtn';
import styles from './index.less';

import {
  risk,
} from './datas';

const TakeRisks = () => {
  const defs = [{
    dataKey: 'score',
    min: 0,
    max: 120,
    nice: false,
    tickCount: 4,
  }];
  const pixelRatio = window.devicePixelRatio * 2;

  function formatLabel(text, index, total) {
    if (index === total - 1) {
      return null;
    }
    return {
      top: true,
    };
  }
  const data = [
    {
      item: 'Users',
      score: 40,
    },
    {
      item: 'weil',
      score: 20,
    },
    {
      item: 'duc',
      score: 80,
    },
  ]

  return (
    <Box title='承受风险' >
      <Chart width="100%" data={data} defs={defs} pixelRatio={pixelRatio} >
        <Axis dataKey="score" label={formatLabel} grid={{ lineDash: null, type: 'arc' }} />
        <Axis dataKey="item" grid={{ lineDash: null }} />
        <Coord type="polar" />
        <Legend />
        <Geom geom="line" position="item*score" color="user" />
        <Geom geom="point" position="item*score" color="user" style={{ stroke: '#FFF', lineWidth: 1 }} />
      </Chart>
      <table className={styles['take-risks-table']}>
        <th>家庭成员</th>
        <th>得分</th>
        <th>风险类型</th>
        <tr>
          <td>234</td>
          <td>234</td>
          <td>234</td>
        </tr>
        <tr>
          <td>12</td>
          <td>12</td>
          <td>12</td>
        </tr>
      </table>
    </Box>
  )
}

