import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { fetchUpdateInfo } from '../../../../services/detail';
import { DecryptDes, GetCookie } from '../../../../utils/func';
import { UserState } from '../../model';
import ActiveInfo from '../../../../components/ActiveInfo';
import EditorInput from '../../../../components/EditorInput';
import styles from '../index.less';

interface WhiteInformationProps {
  user: UserState;
}

const WhiteInformation: React.FC<WhiteInformationProps> = (props) => {
  const { user } = props;
  const { userData } = user;
  const [porscheIsWinner, setPorscheIsWinner] = useState<string>('');
  const [porscheHaveAddress, setPorscheHaveAddress] = useState<string>('');
  const [porscheMemo, setPorscheMemo] = useState<string>('');

  const handlePorscheIsWinner = (val: string) => {
    // 修改中奖情况
    setPorscheIsWinner(val)
    fetchUpdateInfo({
      externalUserId: DecryptDes(GetCookie('externaluserid')),
      porscheIsWinner: '',
    })
  }

  const handlePorscheHaveAddress = (val: string) => {
    // 修改收货地址
    setPorscheHaveAddress(val)
  }

 
  useEffect(() => {

  }, [user])

  return (
    <div>
      <div className={styles.contentBox}>
        <div style={{padding: '0 20px'}}>
          <ActiveInfo title="渠道来源" value={userData && userData.channel} />
          <ActiveInfo title="加好友时间" value={userData && userData.addTime} />
          <ActiveInfo title="报名电话" value={userData && userData.pro.trainingData.phone} />
          <ActiveInfo title="年龄" value={userData && userData.family.age} />
          <ActiveInfo title="已关注公众号" value={userData && userData.subscribe} alignItems="flex-start" />
          <ActiveInfo title="领课情况" value={userData && userData.feimooc.learn ? '已领课' : '未领课'} />
          <ActiveInfo title="用户情况" value={userData && userData.feimooc.userInfo} />
          <ActiveInfo title="保险训练营报名" value={userData && userData.feimooc.inFirstInsure ? '已报名' : '未报名'} />
          <ActiveInfo title="归属导师" value={userData && userData.train.consultantCnName} />
          <ActiveInfo title="定投训练营报名" value={userData && userData.feimooc.fixedInvestmentCamp ? '已报名' : '未报名'} />
          <ActiveInfo title="中奖情况" value={
            <div>
              <EditorInput value={porscheIsWinner || userData && userData.customerInfo.porscheIsWinner} onChange={handlePorscheIsWinner} />
            </div>
          } />
          <ActiveInfo title="收货地址" value={
            <div>
              <EditorInput value={porscheHaveAddress || userData && userData.customerInfo.porscheHaveAddress} onChange={handlePorscheHaveAddress} />
            </div>
          } />
          <ActiveInfo title="备注" value={
            <div>
              <EditorInput value={porscheMemo || userData && userData.customerInfo.porscheMemo} />
            </div>
          } />
        </div>
      </div>
    </div>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(WhiteInformation);