import React, { useState } from 'react';
import { Button } from 'antd-mobile';

import Textarea from '../../../../components/Textarea';
import Search from './Search';
import Mask from '../../../../components/Mask';
import Tip from '../Tip';
import right from '../../../../assets/icons/right.svg';
import styles from './index.less';

interface TransferCustomersProps {
  visible: boolean;
  onCancel: Function;
}

const TransferCustomers: React.FC<TransferCustomersProps> = (props) => {
  const { visible, onCancel } = props;
  const [hasChoose, setHasChoose] = useState<boolean>(true);

  return (
    <Mask title="转接客户" visible={visible} onCancel={onCancel}>
      <div>
        <Tip content="可将客户转接给其他员工，进行后续服务。一个客户最多只能被转接两次，客户被转接成功后，将有90个自然日的服务关系保护期，保护期内的客户无法再次转接。"/>
        <div style={{padding: '0 22px'}}>
          {
            !hasChoose && 
            <>
              <p style={{fontSize: '1rem', color: '#000', marginBottom: '10px'}}>选择成员</p>
              <div>
                <Search/>
                <div className={styles.userLists}>
                  <ul>
                    <li className={styles.active}>
                      <div className={styles.left}>
                        <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="用户头像"/>
                        陈顺下
                      </div>
                      <img src={right} alt="right" className={styles.right}/>
                    </li>
                    <li>
                      <div className={styles.left}>
                        <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="用户头像"/>
                        陈顺下
                      </div>
                      <img src={right} alt="right" className={styles.right}/>
                    </li>
                  </ul>
                </div>
              </div>
              <Button type="primary" className={styles.btn}>确定</Button>
            </>
          }
          {
            hasChoose &&
            <>
              <p style={{fontSize: '1rem', color: '#000', marginBottom: '10px'}}>将转接客户张仁杰给巫春龙</p>
              <p>确认转接后客户将收到以下提示，提示内容根据情况进行调整</p>
              <div style={{height: '7rem'}}>
                <Textarea showCount/>
              </div>
              <div style={{display: 'flex', margin: '5px 0'}}>
                <Button className={styles.btn + ' ' + styles.cancel} style={{flex: 4}} onClick={()=>setHasChoose(false)}>返回</Button>
                <Button type="primary" className={styles.btn} style={{flex: 7}}>确定</Button>
              </div>
            </>
          }
        </div>
      </div>
    </Mask>
  )
}

export default TransferCustomers;