import React from 'react';

import styles from './index.less';

export interface DataItemProps {
  hide?: boolean;
  title: string | number;
  value: string | number;
}

const DataItem: React.SFC<DataItemProps> = (props) => {
  const { title, value, hide } = props;

  return (
    <div style={{visibility: hide ? 'hidden' : 'visible'}} className={styles.dataDetailItem}>
      <p className={styles.line1}>{value}</p>
      <p className={styles.line2}>{title}</p>
    </div>
  )
}

export default DataItem;