import React from 'react';
import { Tabs } from 'antd-mobile';

import TrainDataTabs from './TrainDataTabs';
import TrainActiviteFollowUp from './TrainActiviteFollowUp';
import TrainExplanation from './TrainExplanation';
import TrainDynamic from './TrainDynamic';

interface TrainProps {
  options: any[];
}

const Train: React.SFC<TrainProps> = (props) => {
  const { options } = props;

  return (
    <Tabs tabs={options}>
      {/** 基本资料 */}
      <TrainDataTabs />
      {/** 活动跟进 */}
      <TrainActiviteFollowUp />
      {/** 方案讲解 */}
      <TrainExplanation />
      {/** 客户动态 */}
      <TrainDynamic />
    </Tabs>
  )
}

export default Train;