import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { DecryptDes, GetCookie } from '../../../utils/func';
import { fetchUpdatePlanProInfo, fetchTrainProLogin } from '../../../services/detail';
import { UserState } from '../model';
import { AssetAllocationType } from '../../datas';
import Box from './Box';
import Switch from '../../../components/Switch';
import DatePicker from '../../../components/DatePicker';
import ActiveInfo from '../../../components/ActiveInfo';
import styles from './index.less';

interface TrainPulanOperationProps {
  user: UserState;
}


const TrainPulanOperation: React.SFC<TrainPulanOperationProps> = (props) => {
  const { user } = props;
  const { userData } = user;
  const [effectiveDate, setEffectiveDate] = useState<any>();
  const [assetAllocation, setAssetAllocation] = useState<number>(0);

  // 控制是否存钱
  const handleAllocation = (val: number) => {
    setAssetAllocation(val)
    fetchUpdatePlanProInfo({
      userId: DecryptDes(GetCookie('externaluserid')),
      unionId: DecryptDes(GetCookie('unionid')),
      updKind: 'asset',
      assetAllocation: val
    })
  }

  // 下次有效日期
  const handleChangeDate = (val) => {
    fetchUpdatePlanProInfo({
      userId: DecryptDes(GetCookie('externaluserid')),
      unionId: DecryptDes(GetCookie('unionid')),
      updKind: 'date',
      expireDate: val
    })
  }

  useEffect(() => {
    if (userData) {
      setAssetAllocation(userData.family.assetAllocation)
      fetchTrainProLogin({
        userId: localStorage.getItem('userId'),
        unionid: DecryptDes(GetCookie('unionid')),
        remarkmobile: ''
      })
    }
  }, [user])

  return (
    <Box title="谱蓝实操" extra={<div style={{color: '#1890FF'}}>打开PC页面 &gt;</div>}>
      <div className={styles.trainPulanOperation}>
        <ActiveInfo title="所在城市" value={userData && `${userData.pro.city}`} />
        <ActiveInfo title="客户年龄" value={userData && `${userData.family.age}`} />
        <ActiveInfo title="家庭年收入" value={userData && `${userData.pro.income}`} />
        <ActiveInfo title="年终结余" value={userData && `${userData.pro.AnnualSurplusBegin}元`} />
        <ActiveInfo title="流动资产" value={userData && `${userData.pro.currentAssetsBegin}元`} />
        <ActiveInfo title="谱蓝最新状态" value={userData && `${userData.train.planStage.CreationTime}${userData.train.planStage.DP1}`} />
        <ActiveInfo title="谱蓝剩余使用天数" value={userData && `${userData.family.effectiveRest}天`}/>
        <ActiveInfo title="谱蓝有效止期" value={
          <div style={{width: '5rem'}}>
            <DatePicker value={userData && userData.family.expireDate} showSuffixIcon onChange={handleChangeDate} />
          </div>
        }/>
        <ActiveInfo title="最近打开时间" value={userData && `${userData.family.lastLoginTime}`} />
        <ActiveInfo title="推荐方案" value={userData && `${userData.family.recommendScheme.scheme}`} />
        <ActiveInfo title="方案FYP" value={userData && `${userData.train.recommendProtectSolutionFYP}元`} />
        <ActiveInfo title="存钱" value={
          <div style={{display: 'flex', alignItems: "center"}}>
            {userData && `${AssetAllocationType[assetAllocation]}`}
            <Switch inactiveValue={0} activeValue={1} onChange={handleAllocation} value={assetAllocation}/>
          </div>
        }/>
      </div>
    </Box>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(TrainPulanOperation);