import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { UserState } from './model';
import DataDetail from './components/DataDetail';
import PulanIndex from './components/PulanIndex';
import ColleagAndGroup from './components/ColleagAndGroup';
import styles from './index.less';

const datas = [
  {
    title: 'FYP (元)',
    value: 1000.02
  },
  {
    title: '谱蓝定投 (元)',
    value: 333
  },
  {
    title: '占位符',
    value: 0,
    hide: true
  },
  {
    title: '语音通话次数',
    value: 999
  },
  {
    title: '语音通话时长 (分)',
    value: 156
  },
  {
    title: '累计发音条数',
    value: 156
  },
  {
    title: '累计上课时长 (分)',
    value: 999
  },
  {
    title: '听直播时长 (分)',
    value: 156
  },
  {
    title: '参与活动次数',
    value: 156
  },
]

interface DateCardProps {
  user: UserState
}

const DataCard: React.FC<DateCardProps> = (props) => {
  const { user } = props;
  const [dataVisible, setDataVisible] = useState<boolean>(false);
  const [pulanVisible, setPulanVisible] = useState<boolean>(false);
  const [colAndGroupVisible, setColAndGroupVisible] = useState<boolean>(false);
  
  useEffect(() => {

  }, [user])
  console.log(user.dataOverview)
  return (
    <>
      <DataDetail visible={dataVisible} onCancel={()=>setDataVisible(false)} dataSource={datas}/>
      <PulanIndex visible={pulanVisible} onCancel={()=>setPulanVisible(false)} />
      <ColleagAndGroup visible={colAndGroupVisible} onCancel={()=>setColAndGroupVisible(false)} />
      <div className={styles.dataCardContainer}>
        <div className={styles.line1}>
          <div className={styles.dataItem} onClick={()=>setDataVisible(true)}>
            <p className={styles.p1}>数据概况 &gt;</p>
            <p className={styles.p2}>FYP: {user.dataOverview && user.dataOverview.fyp}</p>
          </div>
          <div className={styles.dataItem}  onClick={()=>setPulanVisible(true)}>
            <p className={styles.p1}>谱蓝指数 &gt;</p>
            <p className={styles.p2}>--</p>
          </div>
        </div>
        <div className={styles.line2} onClick={()=>setColAndGroupVisible(true)}>
          <div className={styles.dataItem}>
            <span>Ta添加的：</span>
            <div className={styles.imgBox}>
              {
                user.dataOverview && user.dataOverview.userList.map((i: any, n: number) => {
                  if (n < 2) {
                    return <img src={i.avatar} key={i.userId} alt="同事头像" />
                  }
                })
              }
            </div>
            共{user.dataOverview ? user.dataOverview.userList.length : 0}个同事
          </div>
          <div className={styles.dataItem}>
            共{user.dataOverview ? user.dataOverview.chatList.length : 0}个群聊 &gt;
          </div>
        </div>
        <div></div>
      </div>
    </>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(DataCard);