import React from 'react';

import styles from './index.less';

export interface DataSourceProps {
  icon: string;
  label: string;
  value: string | number;
}

export interface TabberItemProps {
  dataSource: DataSourceProps[],
  color?: string;
  onClick?: Function;
}

const TabberItem: React.SFC<TabberItemProps> = (props) => {
  const { dataSource, color = '#64646', onClick } = props;

  const handleClick = (val) => {
    if (onClick) {
      onClick(val)
    }
  }

  return (
    <ul className={styles.tabberItem}>
      {
        dataSource.map((i: DataSourceProps) => {
          return (
            <li key={i.value} style={{color: color}} onClick={()=>handleClick(i.value)}>
              <img src={i.icon} alt={i.label} />
              {i.label}
            </li>
          )
        })      
      }
    </ul>
  )
}

export default TabberItem;