import React from 'react';

import Mask from '../../../../components/Mask';
import train from '../../../../assets/icons/train.svg';
import wealth from '../../../../assets/icons/wealth.svg';
import baoshijie from '../../../../assets/icons/baoshijie.svg';
import planpro from '../../../../assets/icons/planpro.svg';
import styles from './index.less';

interface MenusProps {
  visible: boolean;
  onCancel: Function;
  onClick: Function;
}

const MENUSITEM = [
  {
    icon: baoshijie,
    label: '小白营',
    value: 'white',
  },
  {
    icon: train,
    label: '训练营',
    value: 'train'
  },
  {
    icon: planpro,
    label: '专家理财',
    value: 'planpro'
  },
  {
    icon: wealth,
    label: '智能理财',
    value: 'wealth'
  }
]

const Menus: React.SFC<MenusProps> = (props) => {
  const { visible, onCancel, onClick } = props;

  return (
    <Mask position="top" visible={visible} onCancel={onCancel}>
      {visible && <div className={styles.menuContainer}>
        {
          MENUSITEM.map((i: any, n: number) => {
            return (
              <div key={i.label} className={styles.menuItem + ' ' + styles[`menuItem${n}`]} onClick={()=>onClick(i.value)}>
                <img src={i.icon} alt="小白营"/>
                {i.label}
              </div>
            )
          })
        }
      </div> }
    </Mask>
  )
}

export default Menus;