import React from 'react';
import { Tabs } from 'antd-mobile';

import TeacherInformation from './WhiteInformation';
import TeacherDynamic from './WhiteDynamic';

interface TrainProps {
  options: any[];
}

const Train: React.SFC<TrainProps> = (props) => {
  const { options } = props;

  return (
    <Tabs tabs={options}>
      <TeacherInformation/>
      <TeacherDynamic/>
    </Tabs>
  )
}

export default Train;