import React, { ReactNode } from 'react';

import styles from './index.less';

interface TimelineProps {
  time: string;
  title: string | ReactNode | number;
  content: ReactNode | string | number;
}

const Timeline: React.SFC<TimelineProps> = (props) => {
  const { time, title, content } = props;

  return (
    <div className={styles.timeItemContainer}>
      <div className={styles.leftBox}>{time}</div>
      <div className={styles.rightBox}>
        <p className={styles.title}>{title}</p>
        <div className={styles.content}>
          {content}
        </div>
      </div>
    </div>
  )
}

export default Timeline;