import React, { useEffect, useState } from 'react';

import styles from './index.less';

interface SwitchProps {
  activeColor?: string;
  inactiveColor?: string;
  activeText?: string;
  inactiveText?: string;
  disabled?: boolean;
  activeValue?: string | number | object | any[];
  inactiveValue?: string | number | object | any[];
  onChange?: Function;
  value?: string | number;
}

const Switch: React.FC<SwitchProps> = (props) => {
  const { activeColor = "#409EFF", inactiveColor, activeText, inactiveText, disabled, activeValue, inactiveValue, onChange, value } = props;
  const [valueSelf, setValueSelf] = useState<boolean>(false);

  const changeStatus = () => {
    if (valueSelf) {
      onChange(inactiveValue);
    } else {
      onChange(activeValue);
    }
    setValueSelf(!valueSelf)
  }

  useEffect(() => {
    if (activeValue && inactiveValue && value) {
      if (value === inactiveValue) {
        setValueSelf(false)
      } else {
        setValueSelf(true)
      }
    }
  }, [value])

  return (
    <div className={valueSelf ? styles['is-checked'] + ' ' + styles['wk-switch'] : styles['wk-switch']}>
      <input type="checkbox" className={styles['wk-switch_input']}/>
      <span className={(!valueSelf ? styles['is-active'] : ' ') + ' ' + styles['wk-switch_label'] + ' ' + styles['wk-switch_label_left']}>{inactiveText}</span>
      <span className={(disabled ? styles['is-disabled'] : '') + ' ' + styles['wk-switch_core']}
        style={{borderColor: valueSelf ? activeColor : inactiveColor, backgroundColor: valueSelf ? activeColor : inactiveColor, width: '40px'}}
        onClick={changeStatus}
      />
      <span>{activeText}</span>
    </div>
  )
}

export default Switch