import React from 'react';

import remind from '../../../../assets/icons/remind.svg';
import styles from './index.less';

interface TipProps {
  content: string | number;

}

const Tip: React.SFC<TipProps> = (props) => {
  const { content } = props;

  return (
    <div className={styles.tipBox}>
      <img src={remind} alt="提示"/>
      <div>{content}</div>
    </div>
  )
}

export default Tip;