import React from 'react';

import Mask from '../../../../components/Mask';
import Textarea from '../../../../components/Textarea';
import Button from '../../../../components/Button';

interface RemarkProps {
  visible: boolean;
  onCancel: Function;
  type: string;
}

const Remark: React.FC<RemarkProps> = (props) => {
  const { visible, onCancel, type } = props;

  return (
    <Mask title="备注" position="middle" visible={visible} onCancel={onCancel} footer={
      <div style={{display: 'flex', justifyContent: 'flex-end', paddingRight: '10px'}}>
        <Button size="small" style={{marginRight: '5px', marginTop: '0px'}}>取消</Button>
        <Button type="primary" size="small" style={{marginTop: '0px'}}>保存</Button>
      </div>
    }>
      <div style={{margin: '10px', height: '100px'}}>
        <Textarea></Textarea>
      </div>
    </Mask>
  )
}

export default Remark;